.currency__card {
    padding: 25px 20px;
    background: #0d2f52;
    display: flex;
    color: #7dacf3;
}
.currency__card:active, .currency__card:hover {
    outline: 0;
    box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
}
.currency__card p{
    margin-top: 6px;
    font-weight: bold;
    color: #86c551;
}
.right ul{
    text-align: left;
    list-style-type: none;
}
@media screen and (max-width: 1010px) {
    .right{
        text-align: center;
        margin-left: 0;
    }
    .currency__card{
        padding: 5px 20px 0;
        margin-bottom: 5px;
        text-align: center;
    }
}
